.restart-game{
    color: #fff;
    border: 1px solid #1a9139;
    background-color: rgba(21, 127, 85, 1);
    text-transform: uppercase;
}

.restart-game:hover{
    background-color: rgba(11, 104, 58, 1);
}

.back-to-table{
    color: #fff;
    border: 1px solid #af1515;
    background-color: rgba(175, 21, 21, 0.6);
}

.back-to-table:hover{
    background-color: rgba(175, 21, 21, 0.8);
}

/*----------------------------------------------------------------------------------*/

@media only screen and (min-width: 1401px) {
    .restart-game, .back-to-table{
        margin: 0 calc(16vw * .05);
        padding: .5em 1em;
        font-size: calc(30vw * .05);
    }
}

@media only screen and (max-width: 1400px) {
    .restart-game, .back-to-table{
        margin: 0 5px;
        padding: 10px 36px;
        font-size: 18px;
    }
}

@media only screen and (max-width: 500px) {
    .restart-game, .back-to-table{
        padding: 10px calc(30vw * .15);
        font-size: calc(30vw * .15);
    }
}


