.start-game-btn-div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.start-game-btn{
    margin: 10px;
    border: 1px solid black;
    background-color: rgba(239, 238, 232, 0.6);
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
}

.start-game-btn:hover{
    background-color: rgba(239, 238, 232, 0.8);
}

@media only screen and (min-width: 1601px) {
    .start-game-btn{
        font-size: calc(16vw * .08);
        padding: .5em 1em;
    }
}

@media only screen and (max-width: 1600px) {
    .start-game-btn{
        font-size: 20px;
        padding: 10px 10px;
    }
}

@media only screen and (max-width: 500px) {
    .start-game-btn{
        font-size: 1em;
    }
}


