#wager-number{
    display: inline;
    border-bottom: .1em solid #000;
    margin: 0;
}

#wager-intro{
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 0;
}

#wager-input-buttons-div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 5px;
}

.wager-input-button{
    background-color: #fff;
    margin: 0 4px;
}

#wager-input-decrease{
    border: .05em solid #f00;
    color: #f00;
}

#wager-input-decrease:active{
    background-color: #f00; 
    color: #fff;
}

#wager-input-increase{
    border: .05em solid #008000;
    color: #008000;
}

#wager-input-increase:active{
    background-color:#008000; 
    color: white;
}

#wager-bank{
    margin: 0;
    text-align: center;
    color: #8e8e8e;
}

#wager-bank span{
    color: #000;
    font-weight: bold;
}

@media only screen and (min-width: 1441px) {
    #wager-number{
        font-size: calc(16vw * .12);
    }

    #wager-intro, #wager-bank{
        font-size: calc(16vw * .07);;
    }

    .wager-input-button{
        border-radius: 100px;
        width: calc(16vw * .15);
        height: calc(16vw * .15);
        font-size: calc(16vw * .1);
        margin: 0 4px;
    }
}


@media only screen and (max-width: 1440px) {
    #wager-number{
        font-size: 28px;
    }

    #wager-intro, #wager-bank{
        font-size: 16px;
    }

    .wager-input-button{
        border-radius: 100px;
        width: 40px;
        height: 40px;
        font-size: 25px;
        margin: 0 4px;
    }
}

@media only screen and (max-width: 600px) {
    #wager-intro, #wager-bank{
        font-size: 1em;
    }
}


