#roulette{ /*main div of the page - holds everything inthe entire page*/
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

#roulette-table-bg{ /*Background*/
    position: fixed;
    background-color: #308277;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

#roulette-table{ /*Holds the buttons for the bets and div for the wager information*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
}

#roulette-bet-table{ /*the buttons for the bets*/
    margin-top: auto;
    z-index: 1;
    /*transform: rotate(270deg)*/;
}

#roulette-bet-table button{ 
    color: #fff; /*sets the font color for the buttons (not including 'spin')*/
}


#roulette-table-numbers{ /*the div that holds numbers 0-36*/
    width: 200px; /*sets the total width for the div that holds numbers 0-36*/
}

#roulette-table-numbers button{ /*buttons of numbers 0-36*/
    -webkit-transition: opacity .3s;
    -o-transition: opacity .3s;
    transition: opacity .3s;
}

.roulette-number-unselected{ /*other buttons take on this class when they are not selected*/
    opacity: 0.3;
}

#roulette-number-button-0{/*the button for the number 0*/
    display: block;
    margin: auto;
}

.roulette-number-button-green{ /*color for the number 0*/
    background-color: #107c04;
}

.roulette-number-button-red{ /*color for the red numbers*/
    background-color: #990606;
}

.roulette-number-button-black{ /*color for the black numbers*/
    background-color: #0f0f0f;
}

.column-bets{ /*column bet(2to1) buttons*/
    background-color: transparent;
}

#roulette-table-outside-bets{ /*div for the buttons on the left side*/
    position: absolute;
}

.outside-bet{ /*buttons for the even, odd and color bets*/
    background-color: transparent;
}

.color-bet div{ /*the rhombus shape for the color bets*/
    color: transparent;
    -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
            clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}

.color-bet-red div{ /*color for the red bet button*/
    background-color: #990606;
}

.color-bet-black div{ /*color for the black bet button*/
    background-color: #0f0f0f;
}

#roulette-table-dozen-bets button{ /*buttons for the 12s*/
    width: calc((378px - (380px /13))/ 3);
    height: 35px;
}

#roulette-table-placeholder{ /*temporary div to prevent page jump when wager is loaded*/
    margin-bottom: 50px;
    min-height: 133px;
}

#roulette-wager-div{ /*div for the wager informatuin*/
    margin-bottom: 50px;
    min-height: 113px;
    text-align: center;
    z-index: 1;
}

#roulette-wager-div p{
    color: #fff; /*font color for wager information*/
}

#roulette-wager-div p:not(.wager-input){
    padding: 0;
    margin: 0;
    color: #fff;
}

.wager-input{
    margin: 5px 0;
}

#roulette-restart{
    color: #fff;
    border: 1px solid #1a9139;
    background-color: rgba(21, 127, 85, 1);
    text-transform: uppercase;
}

#roulette-restart:hover{
    background-color: rgba(11, 104, 58, 1);
}

#roulette-return{
    color: #fff;
    border: 1px solid #af1515;
    background-color: rgba(175, 21, 21, 0.6);
}

#roulette-return:hover{
    background-color: rgba(175, 21, 21, 0.8);
}

/*-------------SPINNING PAGE ANIMATIONS------------------------*/
@-webkit-keyframes spinWheel{
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg)};
}
@keyframes spinWheel{
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg)};
}

@-webkit-keyframes showRouletteText{
    from {opacity: 0}
    to {opacity: 1}
}

@keyframes showRouletteText{
    from {opacity: 0}
    to {opacity: 1}
}

@-webkit-keyframes dots{
    0% {opacity: 1}
    50% {opacity: 0}
    100% {opacity: 1}
}

@keyframes dots{
    0% {opacity: 1}
    50% {opacity: 0}
    100% {opacity: 1}
}

@-webkit-keyframes showRoulettePick{
    from {opacity: 0}
    to {opacity: 1}
}

@keyframes showRoulettePick{
    from {opacity: 0}
    to {opacity: 1}
}

/*----------------------SPINNING PAGE----------------------------*/
#roulette-wheel-div{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

#roulette-wheel{
    -webkit-animation: hideRouletteWheelDiv 1s ease-in-out 2.2s 1 forwards;
            animation: hideRouletteWheelDiv 1s ease-in-out 2.2s 1 forwards;
}

#roulette-wheel-image{
    margin: auto;
    background-image: url(./wheel.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-animation: showWheel .5s ease-in-out 0s 1, spinWheel 1.0s linear .4s 3, showWheel 1s ease-in-out 2s 1 reverse forwards;
            animation: showWheel .5s ease-in-out 0s 1, spinWheel 1.0s linear .4s 3, showWheel 1s ease-in-out 2s 1 reverse forwards; 
}

#roulette-wheel-text{
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    font-size: 30px;
    -webkit-animation: showRouletteText 1s ease-in-out 0s 1, showRouletteText 1s ease-in-out 2s 1 reverse forwards;
            animation: showRouletteText 1s ease-in-out 0s 1, showRouletteText 1s ease-in-out 2s 1 reverse forwards;
}

.roulette-spinning-dots{
    -webkit-animation-name: dots;
            animation-name: dots;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; 
}

.roulette-spinning-dots:nth-child(1){
    -webkit-animation-delay: .1s;
            animation-delay: .1s
}

.roulette-spinning-dots:nth-child(2){
    -webkit-animation-delay: .3s;
            animation-delay: .3s
}

.roulette-spinning-dots:nth-child(3){
    -webkit-animation-delay: .5s;
            animation-delay: .5s
}

#roulette-picked{
    width: 0;
    height: 0;
    opacity: 0;
    -webkit-animation: hideRouletteWheelDiv .7s ease-in-out 2.5s 1 reverse forwards, showRoulettePick .3s linear 2.2s 1 forwards;
            animation: hideRouletteWheelDiv .7s ease-in-out 2.5s 1 reverse forwards, showRoulettePick .3s linear 2.2s 1 forwards;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

#roulette-picked-square{
    -webkit-clip-path: polygon(0% 0%, 100% 0%, 90% 100%, 10% 100%);
            clip-path: polygon(0% 0%, 100% 0%, 90% 100%, 10% 100%);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

#roulette-picked-square p{
    color: #fff;
    text-align: center;
}

#roulette-picked-outcome{
    opacity: 0;
    text-align: center;
    -webkit-animation: showRoulettePick .3s linear 3.5s 1 forwards;
            animation: showRoulettePick .3s linear 3.5s 1 forwards;
}

#roulette-picked-outcome-lost{
    color: red;
}

#roulette-picked-outcome-won{
    color: green;
}

/*----------------------------------------------------------------------------------*/

@media only screen and (min-width: 1601px) {
    #roulette-bet-table{
        width: calc(18vw);
    }

    #roulette-table-numbers{
        width: calc(18vw);
    }

    #roulette-table-numbers button{
        border: .05em solid #fff;
        height: calc(30vw / 13);
        width: calc(18vw / 3);
        font-size: calc(16vw * .11);
    }

    .outside-bet{
        border: 1px solid #fff;
        font-size: calc(16vw * .1);;
    }

    #roulette-table-outside-bets{
        width: calc(30vw - (30vw / 13));
        -webkit-transform: translate(calc(-30vw * .56), calc(-30vw * .64)) rotate(90deg);
            -ms-transform: translate(calc(-30vw * .56), calc(-30vw * .64)) rotate(90deg);
                transform: translate(calc(-30vw * .56), calc(-30vw * .64)) rotate(90deg);
    }

    #roulette-table-outside-bets div button{
        height: calc(30vw * .1);
    }

    #roulette-table-other-bets button:not(.color-bet){
        width: calc((30vw - (30vw / 13) - 9.3vw) / 2);
    }

    .color-bet{
        width: calc(9.3vw / 2);
    }

    #roulette-table-dozen-bets button{ /*buttons for the 12s*/
        width: calc((30vw - (30vw /13))/ 3);
    }

    #roulette-table-half-bets{ /*div for the half bets*/
        width: calc(30vw - (30vw /13));
        -webkit-transform: translate(calc(30vw * .189), calc(30vw * -.59)) rotate(90deg);
            -ms-transform: translate(calc(30vw * .189), calc(30vw * -.59)) rotate(90deg);
                transform: translate(calc(30vw * .189), calc(30vw * -.59)) rotate(90deg);
    }

    #roulette-table-half-bets button{ /*half bet buttons*/
        width: calc((30vw - (30vw /13)) / 2);
        height: calc(30vw * .1);
    }

    #roulette-wager-div p:not(.wager-text){
        font-size: calc(30vw * .048);
    }

    #roulette-spin{
        font-size: calc(16vw * .08);
        padding: 10px 36px;
    }


    /*-------------SPINNING PAGE ANIMATIONS------------------------*/
    @-webkit-keyframes hideRouletteWheelDiv{
        from {width: 40vw; height: 40vw}
        to {width: 0; height: 0}
    }
    @keyframes hideRouletteWheelDiv{
        from {width: 40vw; height: 40vw}
        to {width: 0; height: 0}
    }

    @-webkit-keyframes showWheel{
        from {width: 0; height: 0}
        to{width: 77%; height: 77%};
    }

    @keyframes showWheel{
        from {width: 0; height: 0}
        to{width: 77%; height: 77%};
    }

    /*---------------------SPINNING PAGE-----------------*/
    #roulette-wheel{
        width: 40vw;
        height: 40vw;
    }

    #roulette-wheel-image{
        width: 77%;
        height: 77%;
    }

    #roulette-wheel-text{
        height: calc(30vw * .1);
        font-size: calc(30vw * .08);
    }

    #roulette-picked-square{
        width: 15vw;
        height: 20vw;
    }

    #roulette-picked-outcome p{
        font-size: calc(30vw * .055); 
    }

    #roulette-picked-square p{
        font-size: 12vw;
    }

    #roulette-picked-outcome p span{
        display: block;
    }

    #roulette-restart, #roulette-return{
        margin: 0 calc(16vw * .05);
        padding: .5em 1em;
        font-size: calc(30vw * .05);
    }
}

@media only screen and (max-width: 1600px) {
    #roulette-bet-table{
        width: 200px;
    }

    #roulette-table-numbers{
        width: 200px;
    }

    #roulette-table-numbers button{
        border: 1px solid #fff;
        height: calc(380px / 13);
        width: calc(200px / 3);
        font-size: 21px;
    }

    .outside-bet{
        border: 1px solid #fff;
        font-size: 18px;
    }

    #roulette-table-outside-bets{
        width: calc(378px - (380px /13));
        -webkit-transform: translate(-218px, -247.5px) rotate(90deg);
            -ms-transform: translate(-218px, -247.5px) rotate(90deg);
                transform: translate(-218px, -247.5px) rotate(90deg);
    }

    #roulette-table-outside-bets div button{
        height: 45px;
    }

    #roulette-table-other-bets button:not(.color-bet){
        width: calc((378px - (380px /13) - 117px) / 2);
    }

    .color-bet{
        width: calc(117px / 2);
    }

    #roulette-table-half-bets{ /*div for the half bets*/
        -webkit-transform: translate(47px, -225px) rotate(90deg);
            -ms-transform: translate(47px, -225px) rotate(90deg);
                transform: translate(47px, -225px) rotate(90deg);
        width: calc(378px - (380px /13));
    }

    #roulette-table-half-bets button{ /*half bet buttons*/
        width: calc((378px - (380px /13)) / 2);
        height: 45px;
    }

    #roulette-spin{
        font-size: 20px;
        padding: 10px 10px;
    }

    /*-------------SPINNING PAGE ANIMATIONS------------------------*/
    @-webkit-keyframes hideRouletteWheelDiv{
        from {width: 450px; height: 450px}
        to {width: 0; height: 0}
    }
    @keyframes hideRouletteWheelDiv{
        from {width: 450px; height: 450px}
        to {width: 0; height: 0}
    }

    @-webkit-keyframes showWheel{
        from {width: 0; height: 0}
        to{width: 350px; height: 350px};
    }

    @keyframes showWheel{
        from {width: 0; height: 0}
        to{width: 350px; height: 350px};
    }

    /*---------------------SPINNING PAGE-----------------*/
    #roulette-wheel{
        width: 450px;
        height: 450px;
    }

    #roulette-wheel-image{
        width: 350px;
        height: 350px;
    }

    #roulette-wheel-text{
        height: 50px;
        font-size: 20px;
    }

    #roulette-picked-square{
        width: 150px;
        height: 200px;
    }

    #roulette-picked-square p{
        font-size: 120px;
    }

    #roulette-picked-outcome p{
        font-size: 23px; 
    }

    #roulette-picked-outcome p span{
        display: block;
    }

    #roulette-restart, #roulette-return{
        margin: 0 5px;
        padding: 10px 36px;
        font-size: 18px;
    }
}

@media only screen and (max-width: 500px) {
    #roulette-bet-table{
        width: 160px;
        -webkit-transform: translate(20px, 0);
            -ms-transform: translate(20px, 0);
                transform: translate(20px, 0)
    }

    #roulette-table-numbers{
        width: 160px;
    }

    #roulette-table-numbers button{
        border: 1px solid #fff;
        height: calc(380px / 13);
        width: calc(160px / 3);
        font-size: 23px;
    }

    .outside-bet{
        border: 1px solid #fff;
        font-size: 18px;
    }

    #roulette-table-outside-bets{
        width: calc(378px - (380px /13));
        -webkit-transform: translate(-208px, -237.5px) rotate(90deg);
            -ms-transform: translate(-208px, -237.5px) rotate(90deg);
                transform: translate(-208px, -237.5px) rotate(90deg);
    }

    #roulette-table-outside-bets div button{
        height: 35px;
    }

    #roulette-table-other-bets button:not(.color-bet){
        width: calc((378px - (380px /13) - 117px) / 2);
    }

    .color-bet{
        width: calc(117px / 2);
    }

    #roulette-table-half-bets{ /*div for the half bets*/
    -webkit-transform: translate(2px, -220px) rotate(90deg);
        -ms-transform: translate(2px, -220px) rotate(90deg);
            transform: translate(2px, -220px) rotate(90deg);
    }

    #roulette-table-half-bets button{ /*half bet buttons*/
        height: 35px;
    }

    #roulette-spin{
        font-size: 1em;
    }

    /*---------------------SPINNING PAGE ANIMATIONS------------------------*/
    @-webkit-keyframes hideRouletteWheelDiv{
        from {width: 90vw; height: 90vw}
        to {width: 0; height: 0}
    }
    @keyframes hideRouletteWheelDiv{
        from {width: 90vw; height: 90vw}
        to {width: 0; height: 0}
    }

    @-webkit-keyframes showWheel{
        from {width: 0; height: 0}
        to{width: 77%; height: 77%};
    }

    @keyframes showWheel{
        from {width: 0; height: 0}
        to{width: 77%; height: 77%};
    }

    /*---------SPINNING PAGE------------------------------*/
    #roulette-wheel{
        width: 90vw;
        height: 90vw;
    }

    #roulette-wheel-image{
        width: 77%;
        height: 77%;
    }

    #roulette-wheel-text{
        height: 50px;
        font-size: 20px;
    }

    #roulette-picked-outcome p{
        margin: 0;
        font-size: 23px; 
    }

    #roulette-picked-outcome p span{
        display: inline;
    }

    #roulette-restart, #roulette-return{
        padding: 10px 20px;
        font-size: .75em;
    }
}


