#end{
    position: absolute;
    background-color: white;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
}

#end-bg{
    position: fixed;
    background-color: #000;
    background: -webkit-repeating-linear-gradient(45deg, #181d26, #181d26 .9%, #131519 1%);
    background: -o-repeating-linear-gradient(45deg, #181d26, #181d26 .9%, #131519 1%);
    background: repeating-linear-gradient(45deg, #181d26, #181d26 .9%, #131519 1%);
    width: 100%;
    height: 100%;
    z-index: -1;
}

#end-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    height: 100%;
    margin: auto;
    text-align: center;
}

.end-heading{
    font-weight: 600;
}

#end-text{
    background-color: #131519;
    color: #fff;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: .5em;
}

.return-to-casino{
    display: inline-block;
    margin: 1em 0;
    padding: .3em;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    border: .1em solid #fff;
    background-color: #131519;
    color: #fff;
}

.return-to-casino:hover{
    background-color: #131519;
    border: .1em solid #666;
    color: #666;
}

@media only screen and (min-width: 1441px) {
    #end-text{
        width: 40%;
    }

    .end-heading{
        font-size: calc(16vw * .18);
    }

    #end-text p:not(.end-heading){
        font-size: calc(16vw * .1);
    }

    .return-to-casino{
        font-size: calc(16vw * .08);
    }
}


@media only screen and (max-width: 1440px) {
    #end-text{
        width: 500px;
    }

    .end-heading{
        font-size: 28px;
    }

    #end-text p:not(.end-heading){
        font-size: 20px;
    }

    .return-to-casino{
        font-size: 20px;
    }
}

@media only screen and (max-width: 600px) {
    #end-text{
        width: 90%;
    }

    .end-heading{
        font-size: calc(16vw * .35);
    }

    #end-text p:not(.end-heading){
        font-size: calc(16vw * .23);
    }

     .return-to-casino{
        font-size: calc(16vw * .3);
    }
}


