.App{
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 100%;
}

#Intro-bg{
    position: fixed;
    background-color: #000;
    background: -webkit-repeating-linear-gradient(45deg, #181d26, #181d26 .9%, #131519 1%);
    background: -o-repeating-linear-gradient(45deg, #181d26, #181d26 .9%, #131519 1%);
    background: repeating-linear-gradient(45deg, #181d26, #181d26 .9%, #131519 1%);
    width: 100%;
    height: 100%;
}

.Intro-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#Intro-contents{
    outline: 1px solid #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 1em;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #131519;
    margin: auto;
}

.Intro-svg{
    outline: 1px solid green;
    width: 100%;
}

#Intro-contents h1{
    color: #fff;
}

#Intro-contents h1 span{
    color: #fff;
}

#Intro-contents p{
    color: #fff;
}

#Intro-contents select{
    display: block;
    margin: auto;
    color: #fff;
    background: #131519; 
    border: .1em solid #fff; 
}

.select-amount{
    color: #45a859
}

.App-note{
    margin-top: auto;
    text-align: center;
}

.links, .skip-div{
    text-align: center;
    margin: 3px 0;
}

.links a, .skip-div a{
    display: inline-block;
    margin: calc(16vw * .03) 0;
    padding: .3em;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    border: .1em solid #fff;
    /*background-color: #131519;*/
    color: #fff;

}

.links a:hover, .skip-div a:hover{
    background-color: #131519;
    border: .1em solid #666;
    color: #666;
}

.links a:after{
    content: "\02192";
}

.skip-div a:after{
    content: " \0003E\0003E";
}

@media only screen and (min-width: 1441px) {
    #Intro-contents{
        width: 30vw;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    #Intro-contents h1{
        font-size: calc(16vw * .1);
    }
        #Intro-contents h1 span {
            font-size: calc(16vw * .25);
            text-shadow: 0 0 .3em #fff, 
                         0 0 .5em #fff, 
                         0 0 .8em #fff, 
                         0 0 1em #228DFF, 
                         0 0 1.2em #228DFF, 
                         0 0 1.5em #228DFF, 
                         0 0 1.8em #228DFF, 
                         0 0 2em #228DFF;
        }

    #Intro-contents p:not(.note), #Intro-contents select{
        font-size: calc(16vw * .07);
    }
    
    .note{
        font-size: calc(16vw * .050);
    }

    .links a{
        font-size: calc(16vw * .08);
    }

    .skip-div a{
        font-size: calc(16vw * .055)
    }
}

@media only screen and (max-width: 1440px) {
    #Intro-contents{
        width: 400px;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    #Intro-contents h1{
        font-size: 20px;
    }

        #Intro-contents h1 span {
            font-size: 60px;
            text-shadow: 0 0 5px #fff, 
                         0 0 10px #fff, 
                         0 0 15px #fff, 
                         0 0 20px #228DFF, 
                         0 0 35px #228DFF, 
                         0 0 40px #228DFF, 
                         0 0 50px #228DFF, 
                         0 0 75px #228DFF;
        }

    #Intro-contents p:not(.note), #Intro-contents select{
        font-size: 16px;
    }
    
    .note{
        font-size: 11px;
    }

    .links a{
        font-size: 20px;
    }

    .skip-div a{
        font-size: 14px;
    }
}

@media only screen and (max-width: 600px) {
    #Intro-contents{
        width: 80vw;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    #Intro-contents h1{
        font-size: calc(16vw * .3);
    }

        #Intro-contents h1 span {
            font-size: calc(16vw * .8);
        }

    #Intro-contents p:not(.note), #Intro-contents select;{
        font-size: calc(16vw * .2);
    }
    
    .note{
        font-size: calc(16vw * .13);
    }

    .links a{
        font-size: calc(16vw * .3);
    }

    .skip-div a{
        font-size: calc(16vw * .2);
    }
}


