.table h1{
    margin: 0;
    padding-top: 10px;
}

.table-games, .table-info{
    /*outline: 1px solid red;*/
    display: -ms-flexbox;
    display: flex;
    margin: auto;
}

#cash-out-disabled{
    color: #b7b7b7;
    text-decoration: line-through;
}

.table-info{
    -ms-flex-direction: row;
        flex-direction: row;
}

.table-info div p{
    margin: 0;
}

.table-game{
    border: 1px solid #4c4c4c;
    padding: 10px;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-decoration: none;
    -webkit-transition: color .4s, border .4s;
    -o-transition: color .4s, border .4s;
    transition: color .4s, border .4s
}

.table-game, .table-game:visited{
    color: #4c4c4c;
}

.table-game:hover, .table-game:active{
    border: 1px solid #000000;
    color: #000000;
}

@media only screen and (min-width: 1441px) {
    .table-info{
        width: 80vw;
    }

    .table-info div{
        padding: calc(16vw * .1) calc(16vw * .2) calc(16vw * .1) 0; 
    }

    .table-info-number{
        font-size: calc(16vw * .12);
    }

    .table-info-type{
        font-size: calc(16vw * .09);
    }

    #cash-out-disabled{
        font-size: calc(16vw * .075);
    }

    #cash-out-enabled{
        font-size: calc(16vw * .1);
    }

    .table-games{
        -ms-flex-direction: row;
            flex-direction: row;
        width: 80vw;
    }
    
    .table-game{
        width: 25vw;
        height: calc(25vw * 1.4)
    }

    .table-game:not(:last-child){
        margin-right: 2.5vw;  
    }
    
    .table-game h2{
        font-size: calc(16vw * .1);
    }

    .table-game p{
        font-size: calc(16vw * .07);
    }
}

@media only screen and (max-width: 1440px) {
    .table-games, .table-info{
        width: 900px;
    }

    .table-info div{
        padding: 20px 30px 20px 0; 
    }

    .table-info-number{
        font-size: 26px;
    }

    .table-info-type{
        font-size: 18px;
    }

    #cash-out-disabled{
        font-size: 16px;
    }

    #cash-out-enabled{
        font-size: 20px;
    }

    .table-game{
        width: 280px;
        height: calc(280px * 1.4)
    }
    .table-game:not(:last-child){
        margin-right: 30px;  
    }
    
    .table-game h2{
        font-size: 18px;
    }

    .table-game p{
        font-size: 13px;
    }
}

@media only screen and (max-width: 901px) {
    .table-games, .table-info{
        width: 100%;
    }

    .table-info-number{
        font-size: calc(16vw * .2);
    }

    .table-info-type{
        font-size: calc(16vw * .14);
    }

    #cash-out-disabled{
        font-size: calc(16vw * .12);
    }

    #cash-out-enabled{
        font-size: calc(16vw * .18);
    }

    .table-game{
        width: 32vw;
        height: calc(30vw * 1.5);
    }
    .table-game:not(:last-child){
        margin-right: 2vw;  
    }
    
    .table-game h2{
        font-size: calc(16vw * .13);
    }

    .table-game p{
        font-size: calc(16vw * .1);
    }
}

@media only screen and (max-width: 600px) {
    .table-info-number{
        font-size: calc(16vw * .4);
    }

    .table-info-type{
        font-size: calc(16vw * .3);
    }

    #cash-out-disabled{
        font-size: calc(16vw * .25);
    }

    #cash-out-enabled{
        font-size: calc(16vw * .35);
    }

    .table-games{
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .table-game{
        width: 100%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .table-game:not(:last-child){
        margin-right: 0;
        margin-bottom: calc(16vw * .5);
    }

    .table-game h2{
        font-size: calc(16vw * .3);
    }

    .table-game p{
        font-size: calc(16vw * .25);
    }
}


