#craps{
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    margin: auto;  
    text-align: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#craps-div{
    margin: auto;
}

#craps p{
    text-transform: uppercase;
}

#craps-main{
    visibility: hidden;
    max-height: 0;
    -webkit-transition: max-height .5s;
    -o-transition: max-height .5s;
    transition: max-height .5s;
}

#craps-dice-div, #craps-rolling-dice-div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin: auto;
    -webkit-transition: width .5s;
    -o-transition: width .5s;
    transition: width .5s;
}

.craps-dice-div-normal-size, #craps-rolling-dice-div{
    width: 100%;
}

.craps-dice-div-small-size{
    width: 25%;
}

.craps-dice, .craps-rolling-dice{
    background-color: #fff;
    -webkit-transition: width .5s, height .5s;
    -o-transition: width .5s, height .5s;
    transition: width .5s, height .5s;
}

.craps-rolling-dice{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.craps-dice:hover{
    background-color: #f9f7ed;
}

.craps-dice-svg{
    width: 100%;
}

.craps-btn{
    margin: 10px;
    border: 1px solid black;
    background-color: rgba(239, 238, 232, 0.6);
    padding: 10px 36px;
    text-align: center;
    font-weight: bold;
}

.craps-btn:hover{
    background-color: rgba(239, 238, 232, 0.8);
}

.craps-outcome{
    margin-bottom: 0; 
}

.craps-outcome-win{
    margin: 0;
    color: green;
}

.craps-outcome-loss{
    margin: 0;
    color: red;
}

.craps-underline{
    text-decoration: underline;
}

#craps-odds{
    margin-bottom: 0;
}

#craps-payout{
    margin-top: 0;
}

#craps-your-bet{
    margin-bottom: 0;
    color:#bbb;
}

#craps-left-dice{
    -webkit-transform: rotate(10deg);
        -ms-transform: rotate(10deg);
            transform: rotate(10deg);
}

#craps-right-dice{
    -webkit-transform: rotate(-15deg);
        -ms-transform: rotate(-15deg);
            transform: rotate(-15deg);
}

@media only screen and (min-width: 1601px) {
    #craps-div{
        width: 50vw;
    }

    .craps-dice, .craps-rolling-dice{
        border: calc(16vw * .008) solid black;
        border-radius: calc(16vw * .1);
    }

    .craps-rolling-dice{
        padding: calc(16vw * .03);
    }

    .craps-dice-normal-size{
        width: calc(16vw * .8);
        height: calc(16vw * .8);
        border-radius: calc(16vw * .1);
    }

    .craps-dice-small-size{
        width: calc(16vw * .25);
        height: calc(16vw * .25);
        border-radius: calc(16vw * .05);
    }

    .craps-dice:first-child{
        margin-top: calc(16vw * .1);
    }

    .craps-outcome, .craps-text{
        font-size: calc(16vw * .07)
    }

    .craps-outcome-win, .craps-outcome-loss{
        font-size: calc(16vw * .1)
    }

    .craps-btn{
        font-size: calc(16vw * .08);
    }

    #craps-your-bet{
        font-size: calc(16vw * .06)
    }

}


@media only screen and (max-width: 1600px) {
    #craps-div{
        width: 500px;
    }

    .craps-dice, .craps-rolling-dice{
        border: 1px solid black;
    }

    .craps-rolling-dice{
        padding: 10px;
    }

    .craps-dice-normal-size{
        width: 150px;
        height: 150px;
        border-radius: 25px;
    }

    .craps-dice-small-size{
        width: 50px;
        height: 50px;
        border-radius: 10px;
    }

    .craps-dice:first-child{
        margin-top: 25px;
    }

    .craps-dice-div-small-size{
        width: 25%;
    }

    .craps-outcome, .craps-text{
        font-size: 15px
    }

    .craps-outcome-win, .craps-outcome-loss{
        font-size: 23px;
    }

    .craps-btn{
        font-size: 18px;
    }

    #craps-your-bet{
        font-size: 14px;
    }

}

@media only screen and (max-width: 901px) {

}

@media only screen and (max-width: 600px) {
    #craps-div{
        width: 90%;
    }

    .craps-rolling-dice{
        padding: calc(16vw * .1);
    }

    .craps-dice:first-child{
        margin-top: calc(16vw * .2);
    }

    .craps-dice-normal-size{
        width: calc(16vw * 2);
        height: calc(16vw * 2);
        border-radius: calc(16vw * .3);
    }

    .craps-dice-small-size{
        width: calc(16vw * .9);
        height: calc(16vw * .9);
        border-radius: calc(16vw * .1);
    }

    .craps-dice-div-small-size{
        width: 45%;
    }

    .craps-btn{
        font-size: calc(16vw * .2);
    }
}


