#blackjack-intro{
    position: absolute;
    background-color: white;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
}

#blackjack-intro-text{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    width: 60%;
    height: 100%;
    margin: auto;
    text-align: center;
}

#blackjack-intro-text div{
    margin: auto;
}

#blackjack-main{
    position: absolute;
    background-color: #efeee8;
    bottom: 0;
    width: 100%;
    height: 100%;
    -webkit-transition: height .5s;
    -o-transition: height .5s;
    transition: height .5s;
    z-index: 1;
}

#blackjack-main-bottom{
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: #fff;
    bottom: 0; 
    width: 100%;
    height: 0;
    -webkit-transition: height .5s;
    -o-transition: height .5s;
    transition: height .5s;
}

#blackjack-game-over-div p{
    text-transform: uppercase;
    text-align: center;
    margin: 0;
    padding: 0;
}

#blackjack-game-replay{
    display: block;
}

#exitLink{
    display: block;
    color: #000;
    font-size: calc(16vw * .1);
}

#blackjack-svg{
    display: block;
    margin: 0 auto;
    height: 100vh;
    width: 100%;
    -webkit-transition: height .5s;
    -o-transition: height .5s;
    transition: height .5s;
}

#blackjack-gameplay-buttons{
    position: absolute;
    bottom: 10%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

#blackjack-win-green{
    color: #007707;
}

#blackjack-loss-red{
    color: #a00000;
}

.deal-btn, #blackjack-gameplay-buttons button{
    margin: 10px;
    text-align: center;
    font-weight: bold;
}

.deal-btn{
    border: 1px solid black;
    background-color: rgba(239, 238, 232, 0.6);
}

#hit{
    color: #f9f9f9;
    background-color: rgba(37, 102, 70, 0.6);
    border: 1px solid #256646;
}

#hit:hover{
    background-color: #256646;
}

#stand{
    color: #f9f9f9;
    background-color: rgba(186, 22, 22, 0.6);
    border: 1px solid #ba1616;
}

#stand:hover{
    background-color: #ba1616;
}

#blackjack-buttons button:hover{
    background-color: rgba(239, 238, 232, 0.8);
}

#blackjack-game-over-btn, #blackjack-replay-btn{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

@media only screen and (min-width: 1441px) {
    #blackjack-intro-text{
        font-size: calc(16vw * .08)
    }

    #blackjack-game-over-div p{
        font-size: calc(16vw * .18);
    }

    .deal-btn, #blackjack-gameplay-buttons button{
        font-size: calc(16vw * .08);
        padding: .5em 1em;
    }
}


@media only screen and (max-width: 1440px) {
    #blackjack-intro-text{
        font-size: 16px;
    }

    #blackjack-game-over-div p{
        font-size: 40px
    }
    
    .deal-btn, #blackjack-gameplay-buttons button{
        font-size: 20px;
        padding: 10px 10px;
    }
}

@media only screen and (max-width: 901px) {
    #blackjack-buttons{

    }
}

@media only screen and (max-width: 600px) {
    #blackjack-intro-text{
        width: 90%;
    }
    
    #blackjack-game-over-div p{
        font-size: calc(16vw * .5)
    }

   .deal-btn, #blackjack-gameplay-buttons button{
        font-size: calc(16vw * .2);
    }
}


